.character-image {
    width: 100%;
    height: 100%;
    min-width: 50px;
    min-height: 50px;
    border: 3px solid #ff3f34;
    border-radius: 5px;
    opacity: .75;
}

.selected-character-image {
    border: 3px solid #0be881;
    opacity: 1;
}

.character-image:hover {
    opacity: 1;
}


